*{
  margin: 0;
  padding: 0;
}
*,
*:before,
*::after{
  box-sizing:inherit;
}

html{
  box-sizing:border-box;
  font-size:62.5%;
}

.App{
  /* display: flex; */
  flex-direction: column;
  width: 69%;
  font-size: 2rem;
  margin: auto;
  margin-top: 5rem;
}

.filter{
  margin: 2rem 0;
  padding: 1rem;
  background-color:antiquewhite;
  border: 1px solid black;
  border-radius: 5px;
}

.save{
  padding: 1rem 2rem;
  background-color:antiquewhite;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 12px gray;
}

input{
  padding: 1rem;
  font-size: 2rem;
  width: 80%;
}

.input-notes{
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

h1{
  font-size: 6rem;
  background-color: antiquewhite;
  text-align: center;
}

.save,.filter:hover{
  background-color: rgb(255, 180, 82);
}

button{
  font-size: 1.8rem;
}

ul li{
  margin-top: 1rem;
}

.important{
  border: 1px solid black;
  border-radius: 5px;
  padding: .4rem;
  margin-left: .6rem;
}

.important:hover{
  background-color: crimson;
  color: aliceblue;
}